.result-container {
  display:flex;
  justify-content:space-between;
  height: 63.98px;
}
.historyContainer {
  display:flex;
  & .historyChild {
    &:nth-child(2) {
      text-align: center;
    }
    &:nth-child(3) {
      text-align: center;
    }
    &:nth-child(4) {
      text-align: center;
    }
    &:nth-child(5) {
      text-align: center;
    }
    &:nth-child(6) {
      text-align: right;
    }
  }
}
.historyChild {
  text-align: left;
  flex-basis: 16.66%;
}
.statContainer {
  display:flex;
  & .statChild {
    &:nth-child(2) {
      text-align: center;
    }
    &:nth-child(3) {
      text-align: left;
      left: 50%;
    }
    &:nth-child(4) {
      text-align: right;
    }
  }
}
.statChild {
  text-align: left;
  flex-basis: 25%;
}
.thirdStatChild {
  position: relative;
  left: 50%;
}